import back from '@/components/back'
export default {
    components:{
        back
    },
	 data(){
	    return{
			info:'',
			defaultImg: 'this.src="' + require('../../assets/images/1.jpg') + '"'
	    }
	},
    created(){
        this.pull()
    },
    methods: {
        pull(){
            this.$get('InspectionAPI/Censorate/QueryCensorateTypeByTypeId',{
                CensorateItemId: this.$route.query.CensorateItemId
            }).then(o => {
                this.info = o
            })
        },
    }
}



